.login-container {
    background-color: rgb(38, 28, 28);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    justify-self: center;
    align-self: center;
    padding: 0px 50px 50px 50px;
    position: relative;
    box-shadow: inset;
}

.login-container > input {
    width: 300px;
    height: 30px;
}

.login-container > button {
    width: 100px;
    height: 30px;
}
